import React from "react"
import moment from "moment"
import { Bar } from "react-chartjs-2"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"

import "tabler-react/dist/Tabler.css";

import { Alert, Page } from "tabler-react";

const chartOptions = {
  responsive: true,
  tooltips: {
      mode: 'x',
      callbacks: {
          title: function(tooltipItems, data) {
            // Pick first xLabel for now
            var title = '';
            var labels = data.labels;
            var labelCount = labels ? labels.length : 0;

            if (tooltipItems.length > 0) {
                var item = tooltipItems[0];

                if (item.xLabel) {
                    title = moment(item.xLabel).locale('zh_cn').format('A h時');
                } else if (labelCount > 0 && item.index < labelCount) {
                    title = labels[item.index];
                }
            }

            return title;
        },
        label: function(tooltipItem, data) {
            if (!tooltipItem.yLabel)
	        return;
	    var label = data.datasets[tooltipItem.datasetIndex].label || '';

            if (label) {
                label += ': ';
            }
            
	    label += '等候超過 ' + Math.round(tooltipItem.yLabel) + '小時';
            return label;
        }
 
      }
  },
  scales: {
            xAxes: [{
                type: "time",
                offset: true,
                gridLines: {
  display: false,
                },
                time: {
                    unit: 'hour',
                    stepSize: 3,
  displayFormats: {
  	hour: 'A h時'
  }
                },
                stacked: true,
                categoryPercentage: 1.0,
                bounds: 'data',
                ticks: {
  source: 'labels'
                }
            }],
            yAxes: [{
                scaleLabel: {
                    display: true,
                    labelString: '最長等候時間（小時）'
                },
                stacked: false,
                gridLines: {
                    display: false,
                },
                ticks: {
                    min: 0,
                    max: 8,
                    fixedStepSize: 2
                }
            }]
        },
        categoryPercentage: 1,
};
const MChart = ({ index, value, labels }) => {
  var data = {
    datasets: [
      {
        data: value,
        label: index +' 平均最長等候時間',
        backgroundColor: "rgba(54, 162, 235, 0.2)"
      }
    ],
    labels: labels
  };
  console.log(data);
  return (<Bar data={data} options={chartOptions} height={70} style={{ maxHeight: '100px' }}/>)
}

const ChartsList = ({ dataMap, labels }) => {
  if (!dataMap)
    return <></>
  var items = [];
  for (var key in dataMap)
    items.push((
      <MChart key={key} index={key} value={dataMap[key]} labels={labels} />
    ));
  return <>{items}</>
}

class AEDlog extends React.Component {
  
  fetchOnline(){    
    var _this = this;    
    axios
      .get(`https://ourhealthcarehk.github.io/api/AEDLOG.json`)
      .then(({ data }) => {
        _this.renderData(data)         
        
      })
      .catch(error => {
        _this.setState({ loading: false, error })
      })
  }
  
  renderData(data){

    var labels = [];
    var dataMap = [];
    var charts = [];
    if (!data)
      return;

    for (var i = 0; i < data.length; i++){
      var DOW = parseInt(data[i].DOW);
      var day = moment().day();
      if ((DOW - day) % 7 !== 0)
        	continue;
      labels.push(moment(data[i].HOD, 'H', 'en'));
      for (var j in data[i]){
      	if (j == 'HOD' || j == 'DOW'){
          continue;
      	}
      	if (!dataMap[j])
      	  dataMap[j] = [];
        dataMap[j][data[i].HOD] = data[i][j];
      }
    }
    this.setState({ dataMap: dataMap, labels: labels, liveDataMap:this.state.liveDataMap })
  }
  
  constructor() {
    super()
    this.state = {
    }
  }
  
	componentDidMount() {
    this.fetchOnline();
	}
  
	render() {
    return (
      <Layout>
    <SEO title="急症室輪候時間" description="各急症室每天在同一時間的平均等候時間。"/>
    <Page.Content title="急症室輪候時間">
      <ChartsList dataMap={this.state.dataMap} labels={this.state.labels} />
    </Page.Content>
      </Layout>
      );
  }
}

export default AEDlog
